body, .root, html{
  padding: 2em;
  /* display: flex; */
  background-color: black;
  color: red;
  height: 100%;
}

button{
  background-color: red;
  border-radius: 1em;
  padding: 1em;
  font-weight: bolder;
  color: white;
  border: none;
  margin-top:0.3em;
  margin-bottom:0.3em;

}
button:hover{
  background-color: white;
  border-radius: 1em;
  padding: 1em;
  font-weight: bolder;
  color: red;
  border: none;
}
.input-field{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
input{
  padding: 1em;
  border-radius: 1em;
  border: none;
  width: 80%;
  margin-top: 0.5em;
  margin-bottom: 0.5em;
}
.card{
  background-color: #641200 !important;
  width: 70%;
  display: flex;
  flex-direction: column;
  padding: 1em;
  justify-content: center;
  align-items: center;
  border-radius: 1em;

}
.card-cont{
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  margin-top: 0.5em;
  margin-bottom: 0.5em;
}
a{
  text-decoration: none;
  color: white;
}
.card-desc{
  color: white;
}